import { useTranslation } from "react-i18next";
import BorderBottom from "../../optimized/Ui/Border/BorderBottom";
import style from "../../optimized/UserStudentMember/StudentProfile/StudentProfile.module.css";
import { Fragment, useCallback } from "react";
import { imageShowUrl } from "../../services/BaseUrl";
import { assestsFeedUrl } from "../../optimized/Services/UrlConfig/AssestsBaseUrl";
import styleForm from "../../optimized/Form/Form.module.css";
import FormCreateButton from "../../optimized/Ui/Button/FormCreateButton";
import CustomCheckbox from "../../optimized/Ui/CustomCheckbox/CustomCheckbox";
import GroupCustomCheckbox from "../../optimized/Ui/CustomCheckbox/GroupCustomCheckbox";
import GroupCustomCheckboxItem from "../../optimized/Ui/CustomCheckbox/GroupCustomCheckboxItem";

function ApplyFormPreview({
  onApplyStudentForm,
  stateLabel,
  state,
  dynamicState,
  otherState,
  extra,
}) {
  const { t } = useTranslation();

  const onViewFile = useCallback((iKey) => {
    if (iKey) {
      const newWindow = window.open(
        `${imageShowUrl}/${iKey}`,
        "_blank",
        "noopener,noreferrer"
      );
      if (newWindow) newWindow.opener = null;
    }
  }, []);
  const onFormSubmit = (e) => {
    e.preventDefault();
    let sm_master = [];
    let mt = null;
    for (let dt of stateLabel) {
      if (dt?.section_key === "subject_criteria") {
        mt = dt;
        break;
      }
    }

    for (let ob in extra) {
      let obj_des = extra[ob];
      if (obj_des?.selected) {
        let gp = mt?.nested_section[obj_des?.whichIndex];
        for (let i = 0; i < gp?.nested_form_checklist?.length; i++) {
          let nest_obj = gp?.nested_form_checklist[i];
          if (nest_obj?.form_checklist_typo === "TEXT") {
            sm_master.push(nest_obj?.form_checklist_subjectId);
          }
        }
        for (let ft in obj_des) {
          if (ft === "selected" || ft === "whichIndex") {
          } else {
            let gh = obj_des[ft];
            if (gh?.or_subject === "FIXED") {
              if (gh?.selected && gh?.id?.length > 0) {
                sm_master.push(...gh?.id);
              }
            } else {
              for (let tt in gh) {
                if (tt === "max" || tt === "min") {
                } else {
                  if (gh[tt]?.selected && gh[tt]?.id) {
                    sm_master.push(gh[tt]?.id);
                  }
                }
              }
            }
          }
        }
      }
    }

    onApplyStudentForm({
      state: state,
      dynamicState: dynamicState,
      otherState: otherState,
      choosenSubject: sm_master,
    });
  };

  return (
    <>
      <form onSubmit={onFormSubmit}>
        <section className={style.sdpa}>
          {otherState?.alreadyPhone ? (
            <div className={style.sdpa_section}>
              <div className={style.sdpac_wrap}>
                <div
                  className={style.sdpas_container}
                  style={{
                    width: "49%",
                  }}
                >
                  <p className={style.sdpasc_label}>
                    {t("form_mobile_no_label")} :
                  </p>
                  <h6 className={style.sdpasc_value}>
                    {otherState?.alreadyPhone}
                  </h6>
                </div>
                {otherState?.allAccount?.map((usr) =>
                  usr?._id === otherState?.selectedUser ? (
                    <div
                      className={style.sdpas_container}
                      style={{
                        width: "49%",
                      }}
                    >
                      <p className={style.sdpasc_label}>
                        {t("existing_account")} :
                      </p>
                      <h6
                        className={style.sdpasc_value}
                      >{`${usr?.userLegalName} - (${usr?.username})`}</h6>
                    </div>
                  ) : (
                    ""
                  )
                )}
              </div>
              <BorderBottom />
            </div>
          ) : null}

          {stateLabel?.map((sl, index) =>
            index === 0 ? (
              <section
                className={styleForm.fcc_section}
                key={`${sl?.section_name}${index}`}
              >
                <div
                  className={styleForm.fccs_inner}
                  style={{
                    justifyContent: "flex-end",
                    marginTop: "1rem",
                  }}
                >
                  {sl?.field_list?.map((fd, d_index) => (
                    <div className={styleForm.fcci_container}>
                      <label
                        htmlFor={`${fd?.db_key}${d_index}`}
                        className={styleForm.fcc_label}
                      >
                        <img
                          className={styleForm.fcc_photo}
                          src={
                            state[fd?.db_key]
                              ? `${imageShowUrl}/${state[fd?.db_key]}`
                              : `${assestsFeedUrl}/feed-user-avatar.svg`
                          }
                          alt="student avatar"
                        />
                      </label>
                      <p>{fd?.label ?? ""}</p>
                    </div>
                  ))}
                </div>
              </section>
            ) : (
              <div
                className={style.sdpa_section}
                key={`${sl?.section_name}${index}`}
              >
                <h5>{sl?.section_name}:</h5>
                {sl?.section_key === "subject_criteria" ? (
                  <div>
                    {sl?.nested_section?.map((ns, ns_index) => (
                      <section
                        className={styleForm.fcc_section}
                        key={`${ns?.nested_section_name}}${ns_index}`}
                        style={{
                          opacity: state?.is_group_select
                            ? extra &&
                              extra[`${ns?.nested_section_name}${ns_index}`] &&
                              extra[`${ns?.nested_section_name}${ns_index}`]
                                ?.selected
                              ? "1"
                              : "0.5"
                            : "1",
                        }}
                      >
                        <GroupCustomCheckbox
                          label=""
                          options={[ns?.nested_section_name]}
                          defaultValue={
                            extra &&
                            extra[`${ns?.nested_section_name}${ns_index}`] &&
                            extra[`${ns?.nested_section_name}${ns_index}`]
                              ?.selected
                              ? extra[`${ns?.nested_section_name}${ns_index}`]
                                  ?.selected
                              : false
                          }
                          onChange={() => {}}
                          customFormInputContainer={{
                            width: "49%",
                          }}
                          isCheck
                          disabled
                        />
                        <GroupCustomCheckboxItem
                          parentKey={`${ns?.nested_section_name}${ns_index}`}
                          options={ns?.nested_form_checklist}
                          defaultValue={
                            extra &&
                            extra[`${ns?.nested_section_name}${ns_index}`]
                          }
                          onChange={() => {}}
                          // errorShow={errorField[fl?.db_key]}
                          customFormInputContainer={{
                            width: "49%",
                            paddingLeft: "2.2rem",
                          }}
                          isCheck
                          disabled
                        />
                      </section>
                    ))}
                  </div>
                ) : sl?.section_key === "academic_details" ? (
                  <div>
                    {sl?.field_list?.map((fl, f_index) => (
                      <Fragment key={`${fl?.db_key}${f_index}`}>
                        <CustomCheckbox
                          label={fl?.label}
                          options={fl?.options}
                          defaultValue={
                            state[fl?.db_key] === "Yes" ? true : false
                          }
                          onChange={() => {}}
                          customFormInputContainer={{
                            width: fl?.width ? `${fl?.width}` : "49%",
                            fontWeight: "500",
                          }}
                          isCheck
                          disabled
                        />
                        {state[fl?.db_key] === "Yes" ? (
                          <>
                            {[
                              "std_tenth_details",
                              "hsc_diploma",
                              "fyjc",
                              "fy_sem_I",
                              "fy_sem_II",
                              "sy_sem_III",
                              "sy_sem_IV",
                              "ty_sem_V",
                              "ty_sem_VI",
                              "msc_mcom",
                            ]?.includes(fl?.db_key) ? (
                              <section
                                style={{
                                  display: "flex",
                                  flexWrap: "wrap",
                                  alignItems: "center",
                                  justifyContent: "flex-start",
                                  gap: "2%",
                                  paddingLeft: "2.2rem",
                                }}
                              >
                                {fl?.nested_form_checklist?.map((gl, g_index) =>
                                  gl?.form_checklist_typo === "TEXT" ? (
                                    <div
                                      className={style.sdpas_container}
                                      key={`${gl?.form_checklist_key}${g_index}`}
                                      style={{
                                        width: gl?.width
                                          ? `${gl?.width}`
                                          : "49%",
                                      }}
                                    >
                                      <p className={style.sdpasc_label}>
                                        {gl?.form_checklist_name} :
                                      </p>
                                      <h6 className={style.sdpasc_value}>
                                        {state[gl?.form_checklist_key]}
                                      </h6>
                                    </div>
                                  ) : gl?.form_checklist_typo === "NUMBER" ? (
                                    <div
                                      className={style.sdpas_container}
                                      key={`${gl?.form_checklist_key}${g_index}`}
                                      style={{
                                        width: gl?.width
                                          ? `${gl?.width}`
                                          : "49%",
                                      }}
                                    >
                                      <p className={style.sdpasc_label}>
                                        {gl?.form_checklist_name} :
                                      </p>
                                      <h6 className={style.sdpasc_value}>
                                        {state[gl?.form_checklist_key]}
                                      </h6>
                                    </div>
                                  ) : null
                                )}
                              </section>
                            ) : (
                              fl?.nested_form_checklist?.map((gl, g_index) => (
                                <section
                                  key={`${gl?.form_checklist_name}${g_index}`}
                                  className={style.fcc_section}
                                  style={{
                                    paddingLeft: "2.2rem",
                                    marginTop: "-1rem",
                                  }}
                                >
                                  <CustomCheckbox
                                    label=""
                                    options={gl?.form_checklist_typo_option_pl}
                                    defaultValue={
                                      state[gl?.form_checklist_key] === "Yes"
                                        ? true
                                        : false
                                    }
                                    onChange={() => {}}
                                    customFormInputContainer={{
                                      width: gl?.width ? `${gl?.width}` : "49%",
                                      fontWeight: "500",
                                    }}
                                    isCheck
                                    disabled
                                  />

                                  {state[gl?.form_checklist_key] === "Yes" ? (
                                    <section
                                      style={{
                                        display: "flex",
                                        flexWrap: "wrap",
                                        alignItems: "center",
                                        justifyContent: "flex-start",
                                        gap: "2%",
                                        paddingLeft: "2.2rem",
                                      }}
                                    >
                                      {gl?.nested_form_checklist_nested?.map(
                                        (jl, j_index) =>
                                          jl?.form_checklist_typo === "TEXT" ? (
                                            <div
                                              className={style.sdpas_container}
                                              key={`${jl?.form_checklist_key}${j_index}`}
                                              style={{
                                                width: jl?.width
                                                  ? `${jl?.width}`
                                                  : "49%",
                                              }}
                                            >
                                              <p className={style.sdpasc_label}>
                                                {jl?.form_checklist_name} :
                                              </p>
                                              <h6
                                                className={style.sdpasc_value}
                                              >
                                                {state[jl?.form_checklist_key]}
                                              </h6>
                                            </div>
                                          ) : jl?.form_checklist_typo ===
                                            "NUMBER" ? (
                                            <div
                                              className={style.sdpas_container}
                                              key={`${jl?.form_checklist_key}${j_index}`}
                                              style={{
                                                width: jl?.width
                                                  ? `${jl?.width}`
                                                  : "49%",
                                              }}
                                            >
                                              <p className={style.sdpasc_label}>
                                                {jl?.form_checklist_name} :
                                              </p>
                                              <h6
                                                className={style.sdpasc_value}
                                              >
                                                {state[jl?.form_checklist_key]}
                                              </h6>
                                            </div>
                                          ) : null
                                      )}
                                    </section>
                                  ) : null}
                                </section>
                              ))
                            )}
                          </>
                        ) : null}
                      </Fragment>
                    ))}
                  </div>
                ) : (
                  <div className={style.sdpac_wrap}>
                    {sl?.field_list?.map((fl, f_index) =>
                      fl?.type === "FILE" ? (
                        <div
                          className={style.sdpas_container}
                          key={`${fl?.db_key}${f_index}`}
                          style={{
                            width: fl?.width ? `${fl?.width}` : "49%",
                          }}
                        >
                          <p className={style.sdpasc_label}>{fl?.label} :</p>
                          {state[fl?.db_key] ? (
                            <h6
                              className={style.sdpasc_value}
                              onClick={() => onViewFile(state[fl?.db_key])}
                              style={{
                                cursor: "pointer",
                                color: "#002BBE",
                              }}
                            >
                              {t("view_file")}
                            </h6>
                          ) : null}
                        </div>
                      ) : fl?.type === "CHECKBOX" ? (
                        <CustomCheckbox
                          key={`${fl?.db_key}${f_index}`}
                          label={fl?.label}
                          options={fl?.options}
                          defaultValue={state[fl?.db_key]}
                          onChange={() => {}}
                          customFormInputContainer={{
                            width: fl?.width ? `${fl?.width}` : "49%",
                          }}
                          customFormInputLabel={{
                            height: "0",
                            margin: "0",
                          }}
                          isCheck
                        />
                      ) : (
                        <div
                          className={style.sdpas_container}
                          key={`${fl?.db_key}${f_index}`}
                          style={{
                            width: fl?.width ? `${fl?.width}` : "49%",
                          }}
                        >
                          <p className={style.sdpasc_label}>{fl?.label} :</p>
                          <h6 className={style.sdpasc_value}>
                            {state[fl?.db_key]}
                          </h6>
                        </div>
                      )
                    )}
                  </div>
                )}

                <BorderBottom />
              </div>
            )
          )}
        </section>
        <FormCreateButton
          label="submit"
          customStyle={{
            width: "97%",
            marginInline: "1rem",
          }}
        />
      </form>
    </>
  );
}

export default ApplyFormPreview;
