import { useTranslation } from "react-i18next";
import style from "./CustomCheckbox.module.css";
import { optionalSubjectCombineWithName } from "../../Constant/Admission/admission_constant";
const GroupCustomCheckboxItem = ({
  options,
  onChange,
  defaultValue,
  customFormInputContainer,
  disabled,
  isCheck,
  parentKey,
  errorShow,
  errorShowKey,
  children,
}) => {
  const { t } = useTranslation();
  return (
    <>
      <div
        className={style.form_input_container}
        style={customFormInputContainer}
      >
        {options?.map((ch, index) =>
          ch?.form_checklist_typo === "TEXT" ? (
            <label
              className={style.ccc}
              key={`${ch?.form_checklist_name}${index}`}
            >
              <span className={style.form_input}>
                {index + 1}). {ch?.form_checklist_name ?? ""}
              </span>
              <input type="checkbox" checked disabled />
              <span className={style.checkmark}></span>
            </label>
          ) : ch?.form_checklist_typo === "SELECT_GROUP" ? (
            <label
              className={style.ccc}
              key={`${ch?.form_checklist_name}${index}`}
            >
              <span className={style.form_input}>
                {index + 1}). {ch?.form_checklist_name ?? ""}
              </span>
              <br />
              <div
                style={{
                  display: "block",
                  marginBottom: "0.4rem",
                }}
              ></div>
              {ch?.form_checklist_typo_option_pl?.map((ft, ft_index) => (
                <label
                  className={style.ccc}
                  key={`${ft?.subjectNam}${ft_index}`}
                  style={{
                    display: "block",
                  }}
                >
                  <span className={style.form_input}>
                    {ft_index + 1}). {ft?.subjectName ?? ""}
                  </span>
                </label>
              ))}
              <input
                type="checkbox"
                checked={
                  isCheck
                    ? defaultValue &&
                      defaultValue[index] &&
                      defaultValue[index]["selected"]
                    : false
                }
                onChange={(e) =>
                  onChange(e, parentKey, index, "dummy", {
                    min: ch?.form_checklist_rule,
                    max: ch?.form_checklist_rule_max,
                    or_subject: "FIXED",
                    fixedSubject: ch?.form_checklist_typo_option_pl,
                  })
                }
                disabled={disabled}
              />
              <span className={style.checkmark}></span>
            </label>
          ) : (
            <label
              className={style.ccc}
              key={`${ch?.form_checklist_name}${index}`}
            >
              <span className={style.form_input}>
                {index + 1}). {ch?.form_checklist_name ?? ""}
                {errorShow && errorShow[`${errorShowKey}${index}`] ? (
                  <span
                    style={{
                      color: "red",
                      marginLeft: "15px",
                    }}
                  >
                    * {errorShow[`${errorShowKey}${index}`]}
                  </span>
                ) : null}
              </span>
              <br />
              <div
                style={{
                  display: "block",
                  marginBottom: "0.4rem",
                }}
              ></div>
              {ch?.form_checklist_typo_option_pl?.map((ft, ft_index) => (
                <label
                  className={style.ccc}
                  key={`${ft?.subjectNam}${ft_index}`}
                  style={{
                    display: "block",
                    marginLeft: "2rem",
                  }}
                >
                  <span className={style.form_input}>
                    {ft_index + 1}). {ft?.subjectName ?? ""}
                  </span>
                  <input
                    type="checkbox"
                    checked={
                      isCheck
                        ? defaultValue &&
                          defaultValue[index] &&
                          defaultValue[index][ft_index] &&
                          defaultValue[index][ft_index]["selected"]
                        : false
                    }
                    onChange={(e) =>
                      onChange(e, parentKey, index, ft_index, {
                        min: ch?.form_checklist_rule,
                        max: ch?.form_checklist_rule_max,
                        masterId: ft?._id,
                      })
                    }
                    disabled={disabled}
                  />
                  <span className={style.checkmark}></span>
                </label>
              ))}
              {ch?.form_checklist_typo_option_pl_optional?.length > 0 ? (
                <label
                  className={style.ccc}
                  style={{
                    display: "block",
                    marginLeft: "2rem",
                  }}
                >
                  <span className={style.form_input}>
                    {ch?.form_checklist_typo_option_pl?.length + 1}).
                    {optionalSubjectCombineWithName(
                      ch?.form_checklist_typo_option_pl_optional
                    )}
                    {errorShow &&
                    errorShow[
                      `${errorShowKey}${index}${ch?.form_checklist_typo_option_pl?.length}OR`
                    ] ? (
                      <span
                        style={{
                          color: "red",
                          marginLeft: "15px",
                        }}
                      >
                        *{" "}
                        {
                          errorShow[
                            `${errorShowKey}${index}${ch?.form_checklist_typo_option_pl?.length}OR`
                          ]
                        }
                      </span>
                    ) : null}
                  </span>

                  <input
                    type="checkbox"
                    checked={
                      isCheck
                        ? defaultValue &&
                          defaultValue[index] &&
                          defaultValue[index][
                            `${ch?.form_checklist_typo_option_pl?.length}OR`
                          ] &&
                          defaultValue[index][
                            `${ch?.form_checklist_typo_option_pl?.length}OR`
                          ]["selected"]
                        : false
                    }
                    onChange={(e) =>
                      onChange(
                        e,
                        parentKey,
                        index,
                        `${ch?.form_checklist_typo_option_pl?.length}OR`,
                        {
                          min: ch?.form_checklist_rule,
                          max: ch?.form_checklist_rule_max,
                          masterId: "",
                        }
                      )
                    }
                    disabled={disabled}
                  />
                  <span className={style.checkmark}></span>
                </label>
              ) : null}

              {defaultValue &&
              defaultValue[index] &&
              defaultValue[index][
                `${ch?.form_checklist_typo_option_pl?.length}OR`
              ] &&
              defaultValue[index][
                `${ch?.form_checklist_typo_option_pl?.length}OR`
              ]["selected"] ? (
                <>
                  {ch?.form_checklist_typo_option_pl_optional?.map(
                    (ft, ft_index) => (
                      <label
                        className={style.ccc}
                        key={`${ft?.subjectNam}${ft_index}`}
                        style={{
                          display: "block",
                          marginLeft: "4.3rem",
                        }}
                      >
                        <span className={style.form_input}>
                          {ft_index + 1}). {ft?.subjectName ?? ""}
                        </span>
                        <input
                          type="checkbox"
                          checked={
                            isCheck
                              ? defaultValue &&
                                defaultValue[index] &&
                                defaultValue[index][
                                  ft_index +
                                    ch?.form_checklist_typo_option_pl?.length
                                ] &&
                                defaultValue[index][
                                  ft_index +
                                    ch?.form_checklist_typo_option_pl?.length
                                ]["selected"]
                              : false
                          }
                          onChange={(e) =>
                            onChange(
                              e,
                              parentKey,
                              index,
                              ft_index +
                                ch?.form_checklist_typo_option_pl?.length,
                              {
                                min: ch?.form_checklist_rule,
                                max: ch?.form_checklist_rule_max,
                                masterId: ft?._id,
                                or_subject: "YES",
                              }
                            )
                          }
                          disabled={disabled}
                        />
                        <span className={style.checkmark}></span>
                      </label>
                    )
                  )}
                </>
              ) : null}
            </label>
          )
        )}
      </div>
    </>
  );
};

export default GroupCustomCheckboxItem;
